import { Component, OnInit } from '@angular/core';
import { Message, RadioButtonModule, ConfirmationService } from 'primeng/primeng';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';
import { GroupMarketPlanInfoBean } from './../bean/custom-group.bean';
import { ActivatedRoute, Params, Router } from '@angular/router';
import * as _ from 'lodash';
import { GroupAdminInfoBean } from './bean/selectGroupAdminInfo.bean';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { FormGroup, FormBuilder } from "@angular/forms";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
  selector: 'app-market-scheme',
  templateUrl: './market-scheme.component.html',
  styleUrls: ['./market-scheme.component.scss'],
  providers: [ConfirmationService],
})
export class MarketSchemeComponent implements OnInit {

  //公共
  tableMesg: any = tableMessage;
  // 标题
  title: string = 'xxx客户群';
  title1: string = '营销方案详情页面';
  // 中间量
  groupName: any = '';
  groupId: any = '';
  //  groupOwnerflag: any = 1;

  // 全局可编辑状态
  editFlag: boolean = true;
  // 隐藏修改删除提交
  normalFlag: boolean = true;

  // bean
  groupMarketPlanInfoBean: GroupMarketPlanInfoBean = new GroupMarketPlanInfoBean();//客户群营销方案详情
  groupAdminInfoBean: GroupAdminInfoBean = new GroupAdminInfoBean();//客户群管理人员查询


  // 群联系人列表
  customGroupContacts: any = [];//联系人列表中间量
  // 中间单位
  channelInfoArr: any = [];//中间单位
  // 客户细分
  customDetailArr: any = [];//客户细分
  // 获客产品方案
  customProductArr: any = [];//获客产品方案
  //获客活动List
  customMarketPlanArr: any = [];//获客活动List
  // 礼品标准列表中间量
  activityNameArr: any = [];//礼品标准列表中间量
  // 控件
  msgs: Message[] = [];
  selectedValue: any = '1'; // 群类型回显中间量
  tionInfo: any = '';//客户细分
  userform: FormGroup;
  message: any = { msg: [] };
  loadingShow: any = { flag: false }; // loading框
  tabMesg: any = tableMessage;//表格中无数据时显示的内容
  dataList: any = [];//生产行
  dataList1: any = [];//生产列
  dataList2: any = [];//非生产行
  dataList3: any = [];//非生产列
  data: any = [];
  undata: any = [];
  paramList: Array<Array<any>> = new Array<Array<any>>();
  unparamList: Array<Array<any>> = new Array<Array<any>>();
  waitSelUserList: any;
  aaaa: boolean = false;
  outVal: any;
  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
    private commonFunc: Customtools,
    public fb: FormBuilder,
    public commfunc: Commfunc,
  ) {
    this.userform = fb.group({
      pointerType: [''],//配置类型
      bussType: [''],//业务类别
    });
  }

  ngOnInit() {
    this.queryClick();
    this.preSet();//初始化
    // this.selectGroupMarketPlanInfo();//客户群营销方案详情查询
    this.selectGroupAdminInfo();//客户群管理人员查询
  }
  //列表查询
  queryClick() {//生产行
    const param = Object.assign({}, this.userform.value);
    param['bussType'] = '0';
    param['pointerType'] = '0';
    param['operationType'] = '3';
    this.httpService.subdibisionConfigMain(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataList = data.resultList ? data.resultList : [];
        this.data = this.dataList.slice(1);
        this.queryClick1();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
      } else {
        this.dataList = [];
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }
  queryClick1() {//生产列
    const param = Object.assign({}, this.userform.value);
    param['bussType'] = '0';
    param['pointerType'] = '1';
    param['operationType'] = '3';
    this.httpService.subdibisionConfigMain(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataList1 = data.resultList ? data.resultList : [];
        this.data.forEach((element, i1) => {
          this.paramList[i1] = [];
          this.dataList1.forEach((element1, i) => {
            let obj = { dimensionId: element.subdibisionId, indicatorsId: element1.subdibisionId, valueType: '', bussType: '0' };
            this.paramList[i1][i] = obj;
          });
        });
        this.queryClick2();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
      } else {
        this.dataList1 = [];
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }
  queryClick2() {//非生产行
    const param = Object.assign({}, this.userform.value);
    param['bussType'] = '1';
    param['pointerType'] = '0';
    param['operationType'] = '3';
    this.httpService.subdibisionConfigMain(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataList2 = data.resultList ? data.resultList : [];
        this.undata = this.dataList2.slice(1);
        this.queryClick3();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
      } else {
        this.dataList2 = [];
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }
  queryClick3() {//非生产列
    const param = Object.assign({}, this.userform.value);
    param['bussType'] = '1';
    param['pointerType'] = '1';
    param['operationType'] = '3';
    this.httpService.subdibisionConfigMain(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataList3 = data.resultList ? data.resultList : [];
        this.undata.forEach((element2, i11) => {
          this.unparamList[i11] = [];
          this.dataList3.forEach((element3, i0) => {
            let zxc = { dimensionId: element2.subdibisionId, indicatorsId: element3.subdibisionId, valueType: '', bussType: '1' };
            this.unparamList[i11][i0] = zxc;
          });
        });
        this.selectGroupMarketPlanInfo();
        this.msgs = [];
        this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
      } else {
        this.dataList3 = [];
        this.msgs = [];
        data.returnCode.message ? data.returnCode.message : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败!' }];
    });
  }
  preSet() {
    // 链接传参
    if (this.routerInfo.snapshot.params['id']) {
      this.groupId = this.routerInfo.snapshot.params['id'];
    }
    if (this.routerInfo.snapshot.params.groupId) {
      this.groupId = this.routerInfo.snapshot.params.groupId;
    }
    if (this.routerInfo.snapshot.params.col) {
      let colTemp = JSON.parse(this.routerInfo.snapshot.params.col);
      this.groupMarketPlanInfoBean.planId = colTemp.planId;
      this.groupMarketPlanInfoBean.statusId = colTemp.statusId;
      if (colTemp.statusId === '1') {
        this.normalFlag = false;
      }
    }
    if (this.routerInfo.snapshot.params.planId) {
      this.groupMarketPlanInfoBean.planId = this.routerInfo.snapshot.params.planId;
    }
    if (this.routerInfo.snapshot.params.statusId) {
      this.groupMarketPlanInfoBean.statusId = this.routerInfo.snapshot.params.statusId;
      if (this.groupMarketPlanInfoBean.statusId === '1') {
        this.normalFlag = false;
      }
    }
    if (this.routerInfo.snapshot.params.groupName) {
      this.groupName = this.routerInfo.snapshot.params.groupName;
    }
    document.getElementById('top').scrollIntoView();//返回顶部 
    // if (this.routerInfo.snapshot.params.groupOwnerflag) {
    //   this.groupOwnerflag = this.routerInfo.snapshot.params.groupOwnerflag;
    // }
    // 中间量回值
    this.groupMarketPlanInfoBean.groupName = this.groupName;
    if (this.groupName) {
      this.title = `${this.groupName}客户群`;//一级标题          
    }
    this.groupMarketPlanInfoBean.groupId = this.groupId;//营销方案群号
    this.groupAdminInfoBean.groupId = this.groupId;//管理人员群号
    // 管理人员
    this.groupAdminInfoBean.roleType = '1';//群联系人类型
    this.groupAdminInfoBean.statusId = '1';//正常状态
    // 初始化
    // 群类型内容回显
    this.selectedValue = this.groupMarketPlanInfoBean.groupType;//群类型内容回显
  }
  reAssignment() {
    //重赋值
    for (let i in this.groupMarketPlanInfoBean.linkmanList) {
      this.groupMarketPlanInfoBean.linkmanList[i] = {
        // 联系人重赋值
        serialNum: '',
        areaName: this.groupMarketPlanInfoBean.linkmanList[i].areaName,
        linkMan: this.groupMarketPlanInfoBean.linkmanList[i].linkMan,
        ContactsItem1: this.customGroupContacts[i].ContactsItem1,
        customItem1: this.customGroupContacts[i].customItem1,
      }
    }
    this.activityNameArr = [];
    for (let i in this.groupMarketPlanInfoBean.activityList) {
      // 获客活动List礼品标准
      this.activityNameArr.push(this.groupMarketPlanInfoBean.activityList[i].activityName);
    }
    this.customMarketPlanArr = [
      // 获客活动List
      {
        name: '礼品标准',
        context: this.activityNameArr.toString(),
      },
    ];
    for (let i in this.groupMarketPlanInfoBean.activityList) {
      // 获客活动List活动方案
      let n = parseInt(i) + 1;
      this.customMarketPlanArr.push({ name: `活动方案${n}`, context: this.groupMarketPlanInfoBean.activityList[i].activityDesc }) // 获客活动List重赋值
    }
  }
  selectGroupMarketPlanInfo() {
    // 客户群营销方案详情查询
    this.httpService.selectGroupMarketPlanInfo(this.groupMarketPlanInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupMarketPlanInfoBean = data;
        this.groupMarketPlanInfoBean.bankList = data.bankList;//同业List
        this.groupMarketPlanInfoBean.activityList = data.activityList;//活动List
        this.selectedValue = this.groupMarketPlanInfoBean.groupType;//群类型中间量赋值
        //生产经营性客户细分
        this.data.forEach((element, i1) => {
          this.paramList[i1] = [];
          this.dataList1.forEach((element1, i) => {//14
            let obj;
            this.groupMarketPlanInfoBean.operationInfoList.forEach(element3 => {//12
              if ((element3.dimensionId == element.subdibisionId) && (element3.indicatorsId == element1.subdibisionId)) {
                obj = { dimensionId: element.subdibisionId, indicatorsId: element1.subdibisionId, valueType: element3.value, bussType: '0' };
              }
            });
            if (obj) {
              this.paramList[i1][i] = obj;
            } else {
              this.paramList[i1][i] = { dimensionId: element.subdibisionId, indicatorsId: element1.subdibisionId, valueType: '', bussType: '0' };
            }
          });
        });
        //非生产经营性客户细分
        this.undata.forEach((element, i1) => {
          this.unparamList[i1] = [];
          this.dataList3.forEach((element1, i) => {
            let obj;
            this.groupMarketPlanInfoBean.unoperationInfoList.forEach(element3 => {//12
              if ((element3.dimensionId == element.subdibisionId) && (element3.indicatorsId == element1.subdibisionId)) {
                obj = { dimensionId: element.subdibisionId, indicatorsId: element1.subdibisionId, valueType: element3.value, bussType: '1' };
              }
            });
            if (obj) {
              this.unparamList[i1][i] = obj;
            } else {
              this.unparamList[i1][i] = { dimensionId: element.subdibisionId, indicatorsId: element1.subdibisionId, valueType: '', bussType: '1' };
            }
          });
        });
        // this.reAssignment();//重赋值
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询详情失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }
  selectGroupAdminInfo() {
    // 客户群管理人员查询
    this.httpService.selectGroupAdminInfo(this.groupAdminInfoBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupAdminInfoBean = data;
        this.groupAdminInfoBean.roleList = data.roleList;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }
  returnGroupView() {
    // 返回客户群视图
    if (this.editFlag == false) {
      this.confirmationService.confirm({
        message: '编辑的内容未保存，确认返回吗?',
        accept: () => {
          this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
        }
      });
    }
    else {
      this.confirmationService.confirm({
        message: '返回客户群视图？',
        accept: () => {
          this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
        }
      });
    }
  }
  submit() {
    // 提交
    // if (this.editFlag == false) {
    //   this.confirmationService.confirm({
    //     message: '编辑的内容未保存，确认提交吗?',
    //     accept: () => {
    //       this.addGroupMarketPlanInfo();
    //       this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
    //     }
    //   });
    // }
    // else {
    let tempFlag1 = true;
    tempFlag1 = this.isNull();//非空校验
    if (!tempFlag1) {
      return;
    }
    this.confirmationService.confirm({
      message: '是否提交并返回客户群视图？',
      accept: () => {
        this.waitSelUserList = this.commfunc.query('QXXBGLC', {});
        if (!this.waitSelUserList.erro) {
          this.outVal = this.waitSelUserList.waitSelUserList;
          this.aaaa = this.waitSelUserList.display;
          let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          let a = commonHeader.extParam.specifyNextOpers;
          if (this.aaaa == true && (a == undefined || a == '' || a == null)) {
            this.aaaa = true;
          } else {
            this.addGroupMarketPlanInfo(3);
          }
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
        }
      }
    });
    // }
  }

  outDisplayCall(event) {
    this.aaaa = event;
  }

  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.addGroupMarketPlanInfo(3);
  }

  addGroupMarketPlanInfo(i) {
    this.editFlag = true;
    this.groupMarketPlanInfoBean.operationInfoList = [];
    this.groupMarketPlanInfoBean.unoperationInfoList = [];
    this.paramList.forEach((element, i1) => {
      let obj = this.paramList[i1];
      obj.forEach(i => {
        let str = { dimensionId: i.dimensionId, indicatorsId: i.indicatorsId, value: i.valueType, bussType: '0' };
        this.groupMarketPlanInfoBean.operationInfoList.push(str);
      })
    });
    this.unparamList.forEach((c, e) => {
      let qwe = this.unparamList[e];
      qwe.forEach(w => {
        let asd = { dimensionId: w.dimensionId, indicatorsId: w.indicatorsId, value: w.valueType, bussType: '1' };
        this.groupMarketPlanInfoBean.unoperationInfoList.push(asd);
      })
    });
    this.groupMarketPlanInfoBean.groupType = this.selectedValue;//群类型中间量虫赋值
    this.groupMarketPlanInfoBean.statusId = i;
    this.httpService.addGroupMarketPlanInfo(this.groupMarketPlanInfoBean).subscribe(data => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        if (i == 3) {
          this.msgs = [{ severity: 'success', summary: '提示', detail: '提交成功!' }];
        } else if (i == 2) {
          this.msgs = [{ severity: 'success', summary: '提示', detail: '保存成功!' }];
        }
        window.setTimeout(() => {
          this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
        }, 500)
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '提交失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
    })
  }
  deleteGroupMarketPlanInfo() {
    // 删除
    this.confirmationService.confirm({
      message: '是否删除并返回客户群视图？',
      accept: () => {
        this.groupMarketPlanInfoBean.statusId = '1';//删除-状态默认
        this.httpService.deleteGroupMarketPlanInfo(this.groupMarketPlanInfoBean).subscribe(data => {
          if (data.returnCode.code == 'success') {

            this.msgs = [];
            this.msgs = [{ severity: 'success', summary: '提示', detail: '删除成功!' }];
            this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view', { groupId: this.groupId, groupName: this.groupName }]);
          }
          else {
            this.msgs = [];
            data.returnCode.code ? data.returnCode.code : '删除失败！';
            this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
          }
        }, err => {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
      }
    });
  }
  editGroupMarketPlanInfo() {
    // 编辑触发
    this.editFlag = false;
    // if (!this.groupMarketPlanInfoBean.bankList || this.groupMarketPlanInfoBean.bankList.length == 0) {
    //   this.bankListPre();//同业预初始
    //   this.reAssignment();//重赋值
    // }
    // if (!this.groupMarketPlanInfoBean.activityList || this.groupMarketPlanInfoBean.activityList.length == 0) {
    //   this.activityListPre();//活动预初始
    //   this.reAssignment();//重赋值
    // }
  }
  saveGroupMarketPlanInfo() {
    // 保存触发
    // let tempFlag1 = true;
    // tempFlag1 = this.isNull();//非空校验
    // if (!tempFlag1) {
    //   return;
    // }
    this.editFlag = true;
    this.addGroupMarketPlanInfo(2);
  }
  addbankListArray() {
    //同业加行
    if (this.groupMarketPlanInfoBean.bankList) {
      // 同业
      for (let i in this.groupMarketPlanInfoBean.bankList) {
        if (this.groupMarketPlanInfoBean.bankList[i].bankName == '' || this.groupMarketPlanInfoBean.bankList[i].advantageDesc == '' || this.groupMarketPlanInfoBean.bankList[i].chanceDesc == '') {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '请逐条增添，前一条数据不能填空！' }];
          return;
        }
      }
    }

    let tempArr1 = [];
    let tempArr2 = {};
    tempArr1 = this.groupMarketPlanInfoBean.bankList;

    // 序号未定
    for (let n in tempArr1) {

      tempArr2 = {
        serialNum: '',
        bankName: '',
        advantageDesc: '',
        chanceDesc: '',
      }
    }
    tempArr1.push(tempArr2);
    this.groupMarketPlanInfoBean.bankList = _.clone(tempArr1);
  }
  delbankListArray(i) {
    //同业减行
    this.groupMarketPlanInfoBean.bankList.splice(i, 1);
    let tmpArr = _.clone(this.groupMarketPlanInfoBean.bankList);
    this.groupMarketPlanInfoBean.bankList = tmpArr;

  }
  addactivityListArray() {
    //活动List加行
    if (this.groupMarketPlanInfoBean.activityList) {
      // 活动
      for (let i in this.groupMarketPlanInfoBean.activityList) {
        if (this.groupMarketPlanInfoBean.activityList[i].activityName == '' || this.groupMarketPlanInfoBean.activityList[i].activityDesc == '') {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '请逐条增添，前一条数据不能填空！' }];
          return;
        }
      }
    }

    let tempArr1 = [];
    let tempArr2 = {};
    tempArr1 = this.groupMarketPlanInfoBean.activityList;

    // 序号未定
    for (let n in tempArr1) {

      tempArr2 = {
        serialNum: '',
        activityName: '',
        activityDesc: '',
      }
    }
    tempArr1.push(tempArr2);
    this.groupMarketPlanInfoBean.activityList = _.clone(tempArr1);
  }
  delactivityListArray(i) {
    //活动List减行
    this.groupMarketPlanInfoBean.activityList.splice(i, 1);
    let tmpArr = _.clone(this.groupMarketPlanInfoBean.activityList);
    this.groupMarketPlanInfoBean.activityList = tmpArr;

  }

  // 其他
  // 非空校验
  isNull() {
    if (!this.groupMarketPlanInfoBean.marketPlanName || this.groupMarketPlanInfoBean.marketPlanName.search(/^\S+$/)) {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '营销方案名不能为空' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.keymanInfo) {
      // 关键人
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '关键人不能填空！' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.needInfo) {
      // 细分需求
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '细分需求不能填空！' }];
      return false;
    }
    if (this.groupMarketPlanInfoBean.bankList) {
      // 同业
      if (this.groupMarketPlanInfoBean.bankList.length === 0) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '同业列表不能为空，请至少填写一条记录！' }];
        return false;
      }
      for (let i in this.groupMarketPlanInfoBean.bankList) {
        if (this.groupMarketPlanInfoBean.bankList[i].bankName == '' || this.groupMarketPlanInfoBean.bankList[i].advantageDesc == '' || this.groupMarketPlanInfoBean.bankList[i].chanceDesc == '') {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '同业列表不能填空，请填写完整或删除空行！' }];
          return false;
        }
      }
    }
    if (!this.groupMarketPlanInfoBean.targetInfo) {
      // 开发目标
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '开发目标不能填空！' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.mainProdInfo) {
      // 主打产品
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '主打产品不能填空！' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.creditProdInfo) {
      // 授信产品参考方案
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '授信产品参考方案不能填空！' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.riskInfo) {
      // 授信风险点及规避方式
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '授信风险点及规避方式不能填空！' }];
      return false;
    }
    if (!this.groupMarketPlanInfoBean.giftInfo) {
      // 礼品标准
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '礼品标准不能填空！' }];
      return false;
    }
    if (this.groupMarketPlanInfoBean.activityList) {
      // 活动
      if (this.groupMarketPlanInfoBean.activityList.length === 0) {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '活动列表不能为空，请至少填写一条记录！' }];
        return false;
      }
      for (let activity of this.groupMarketPlanInfoBean.activityList) {
        if (!activity.activityName || !activity.activityDesc) {
          this.msgs = [];
          this.msgs = [{ severity: 'error', summary: '提示', detail: '活动列表不能填空，请填写完整或删除空行！' }];
          return false;
        }
      }
    }
    return true;
  }
}
