import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service'
import { GroupMarketPlanBean } from './../../bean/custom-group.bean'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { SelectItem, Message } from 'primeng/primeng';
import { MessageService } from 'primeng/components/common/messageservice';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";

@Component({
  selector: 'app-market-scheme-search',
  templateUrl: './market-scheme-search.component.html',
  styleUrls: ['./market-scheme-search.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class MarketSchemeSearchComponent implements OnInit {

  //公共
  tableMesg: any = tableMessage;
  customGroup: string = "XXX客户群";
  marketSchemeSearch: string = "营销方案历史记录";
  arr: any;
  cars: any;
  groupMarketPlan: GroupMarketPlanBean = new GroupMarketPlanBean();// 客户群营销方案
  startDate: string = '';
  endDate: string = '';

  // 组件
  state: SelectItem[];
  creater: SelectItem[];
  msgs: Message[] = [];

  groupId : string;
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routerInfo : ActivatedRoute,
    public CommonHttpService : CommonHttpService
  ) {
    this.arr = [
      {
        ch: '营销方案名称',
        label: 'YXFAMC'
      },
      {
        ch: '营销方案状态',
        label: 'YXFAZT'
      },
      {
        ch: '创建人',
        label: 'CJR'
      },
      {
        ch: '创建日期',
        label: 'CJMC'
      },
      {
        ch: '生效日期',
        label: 'SHENGXRQ'
      },
      {
        ch: '失效日期',
        label: 'SHIXRQ'
      },
    ];
    this.cars = [
      {
        YXFAMC: 'YXFAMC',
        YXFAZT: 'YXFAZT',
        CJR: 'CJR',
        CJMC: 'CJMC',
        SHENGXRQ: 'SHENGXRQ',
        SHIXRQ: 'SHIXRQ',
      },
      {
        YXFAMC: 'YXFAMC',
        YXFAZT: 'YXFAZT',
        CJR: 'CJR',
        CJMC: 'CJMC',
        SHENGXRQ: 'SHENGXRQ',
        SHIXRQ: 'SHIXRQ',
      },
      {
        YXFAMC: 'YXFAMC',
        YXFAZT: 'YXFAZT',
        CJR: 'CJR',
        CJMC: 'CJMC',
        SHENGXRQ: 'SHENGXRQ',
        SHIXRQ: 'SHIXRQ',
      },
      {
        YXFAMC: 'YXFAMC',
        YXFAZT: 'YXFAZT',
        CJR: 'CJR',
        CJMC: 'CJMC',
        SHENGXRQ: 'SHENGXRQ',
        SHIXRQ: 'SHIXRQ',
      }
    ];
  }
  ngOnInit() {
    this.groupMarketPlan.creator = '1';
    this.groupMarketPlan.pageNum = 1;
    this.groupMarketPlan.pageSize = 10;
    this.groupMarketPlan.state = '1';
    this.groupId = this.routerInfo.snapshot.params['groupId'] || this.routerInfo.snapshot.params['id']
    this.state = [];
    this.state.push({ label: '未审核', value: '1' }, { label: '已审核', value: '2' })
    this.creater = [];
    this.creater.push({ label: '张三', value: '1' }, { label: '李四', value: '2' });
    this.selectGroupMarketPlanCreator();
  }
  selGroupMarketPlan() {
    // 客户群营销方案查询
    // this.groupMarketPlan.marketPlanName = '1';
    // this.groupMarketPlan.groupId = 'admin1';
    // this.groupMarketPlan.groupName = '1';
    // this.groupMarketPlan.startDate = '';
    // this.groupMarketPlan.endDate = '';
    this.groupMarketPlan.startDate = this.startDate;//开始时间绑定
    this.groupMarketPlan.endDate = this.endDate;  //结束时间绑定
    let param = _.clone(this.groupMarketPlan);
    if (this.groupMarketPlan.groupId == null || this.groupMarketPlan.groupName == '') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '客户群名称不能为空！' });

      return;
    }
    // if (this.groupMarketPlan.groupName == null || this.groupMarketPlan.groupName == '') {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '提示', detail: '客户群名称不能为空！' });
    //   return;
    // }
    // if (this.groupMarketPlan.marketPlanName == null || this.groupMarketPlan.marketPlanName == '') {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '提示', detail: '营销方案名称不能为空！' });
    //   return;
    // }
    if (this.groupMarketPlan.startDate == null || this.groupMarketPlan.startDate == '') {
      param.startDate = '10001010';//默认开始时间
    }
    else {
      param.startDate = this.commfunc.transDate(param.startDate);//选择开始时间
    }
    if (this.groupMarketPlan.endDate == null || this.groupMarketPlan.endDate == '') {
      param.endDate = '90001010';//默认结束时间
    }
    else {
      param.endDate = this.commfunc.transDate(param.endDate);//选择结束时间
    }

    //  param = {
    //   groupId: this.groupMarketPlan.groupId,
    //   groupName: this.groupMarketPlan.groupName,
    //   marketPlanName: this.groupMarketPlan.marketPlanName,
    //   state: this.groupMarketPlan.state,      
    //   startDate: this.groupMarketPlan.startDate,
    //   endDate: this.groupMarketPlan.endDate,
    //   pageNum: this.groupMarketPlan.pageNum,
    //   pageSize: this.groupMarketPlan.pageSize,
    // }

    let obs = this.httpService.selectGroupMarketPlan(param);
    obs.subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupMarketPlan.planList = data.planList;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  paginate(event) {
    // 分页
    this.groupMarketPlan.pageNum = event.page + 1;
    this.groupMarketPlan.pageSize = event.rows;
    this.selGroupMarketPlan();//客户群营销方案查询
  }

  //人员查询
  selectGroupMarketPlanCreator(){
    let param = {
      groupId : this.groupId
    }
    this.httpService.selectGroupMarketPlanCreator(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.creater = [];
        data.infoList.forEach(item => {
          this.queryTellerById(item);
        });
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
     //查询人员姓名
     queryTellerById(item){
      let params = {
        tellerId : item.creator    
      }
      let successFnc = data => {
        this.creater.push({label : data.tellerName,value : item.creator })
        this.creater = _.clone(this.creater)
      }
      this.commfunc.interfaceComFnc(this.CommonHttpService.queryTellerById(params),successFnc,this.msgs)
    }

}
